.container {
  background-color: rgb(243, 204, 153);
  padding: 10px;
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: #ffffff;
  height: 400px;
  width: 70%;
}

.container::-webkit-scrollbar {
  width: 5px;
}
.container::-webkit-scrollbar-track {
  background: #b9a6fe;
}
.container::-webkit-scrollbar-thumb {
  width: 3px;
  background: #b9a6fe;
  border-radius: 6px;
}
.container::-webkit-scrollbar-thumb:hover {
  background: #b9a6fe;
}

.container::-webkit-scrollbar {
  width: 8px;
}

.container::-webkit-scrollbar-track {
  background: gray;
}

.container::-webkit-scrollbar-thumb {
  background: #b9a6fe;
}

.container::-webkit-scrollbar-thumb:hover {
  background: #000000;
}

