.triangle {
  position: absolute;
  width: 0;
  height: 0;
  clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
  z-index: 3;
}

.text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  color:black;
  font-weight: bold;
}

